import React, { useState, useEffect } from 'react';
import MonacoEditor from '@monaco-editor/react';
import NavBar from './components/NavBar';
import { mergeXml } from './utils/mergeXml';

const App = () => {
  const [xml1, setXml1] = useState('');
  const [xml2, setXml2] = useState('');
  const [mergedXml, setMergedXml] = useState('');

  useEffect(() => {
    import('monaco-editor/esm/vs/editor/editor.api').then((monaco) => {
      monaco.languages.register({ id: 'xml2' });
      monaco.languages.setMonarchTokensProvider('xml2', {
        tokenizer: {
          root: [
            [/<\?xml.*\?>/, 'metatag'],
            [/(<)([\w:]+)(\s*)(\/?>)/, ['delimiter', 'tag', '', 'delimiter']],
            [/(<\/)([\w:]+)(\s*)(>)/, ['delimiter', 'tag', '', 'delimiter']],
            [/[^\s"']+/, 'attribute.value'],
            [/["']/, 'delimiter'],
          ],
        },
      });

      // Register folding range provider for XML
      monaco.languages.registerFoldingRangeProvider('xml2', {
        provideFoldingRanges: (model) => {
          const text = model.getValue();
          const ranges = [];
          const tagRegexp = /<([\w:-]+)/g;
          let match;
          while ((match = tagRegexp.exec(text))) {
            const startLine = model.getPositionAt(match.index).lineNumber;
            const endLine = model.getPositionAt(match.index + match[0].length).lineNumber;
            ranges.push({ start: startLine, end: endLine });
          }
          return ranges;
        },
      });
    });
  }, []);

  const handleFileUpload = (event, setXml) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = (e) => {
      setXml(e.target.result);
    };
    reader.readAsText(file);
  };

  const handleMerge = () => {
    const merged = mergeXml(xml1, xml2);
    setMergedXml(merged);
  };

  const handleDownload = () => {
    const element = document.createElement('a');
    const file = new Blob([mergedXml], { type: 'text/xml' });
    element.href = URL.createObjectURL(file);
    element.download = 'merged.xml';
    document.body.appendChild(element);
    element.click();
  };

  return (
    <div>
      <NavBar />
      <div className="container mx-auto p-4">
        <div className="flex justify-between mb-4">
          <input
            type="file"
            onChange={(e) => handleFileUpload(e, setXml1)}
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
          />
          <input
            type="file"
            onChange={(e) => handleFileUpload(e, setXml2)}
            className="bg-cyan-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline ml-auto"
          />
        </div>
        <div className="flex flex-wrap mb-4">
          <div className="flex-1 w-full md:w-1/2">
            <MonacoEditor
             height="40vh"
            
              language="xml"
              theme="vs-dark"
              value={xml1}
              onChange={setXml1}
              options={{ automaticLayout: true }}
            />
          </div>
          <div className="flex-1 w-full md:w-1/2">
            <MonacoEditor
             height="40vh"
              language="xml"
              theme="vs-dark"
              value={xml2}
              onChange={setXml2}
              options={{ automaticLayout: true }}
            />
          </div>
        </div>
        <button
          className="bg-blue-500 text-white py-2 px-4 rounded mb-4"
          onClick={handleMerge}
        >
          Merge
        </button>
        <div className="flex-1 w-full">
          <MonacoEditor
           height="40vh" 
            language="xml"
            theme="vs-dark"
            value={mergedXml}
            onChange={setMergedXml}
            options={{ automaticLayout: true }}
          />
        </div>
        <button
          className="bg-green-500 hover:bg-green-700 text-white font-bold py-2 px-4 rounded mb-4"
          onClick={handleDownload}
          disabled={!mergedXml}
        >
          Download
        </button>
      </div>

      <div className="container mx-auto my-4">
        <footer className="text-center mt-8">
          Made in <a href="https://en.wikipedia.org/wiki/New_Hampshire" target="_blank" rel="noopener noreferrer" className="underline">New Hampshire</a> with Love <span role="img" aria-label="Love emoji"></span>
        </footer>

      </div>
    </div>
  );
};

export default App;
