// src/utils/mergeXml.js
import { DOMParser, XMLSerializer } from 'xmldom';
import { parseString } from 'xml2js';
import { Builder } from 'xml2js';

export const mergeXml = (xml1, xml2) => {
  const mergedTypes = {};

  const addMembers = (type, members) => {
    if (!mergedTypes[type]) {
      mergedTypes[type] = new Set();
    }
    members.forEach((member) => mergedTypes[type].add(member));
  };

  const parseXml = (xml) => {
    parseString(xml, (err, result) => {
      if (err) {
        console.error('Error parsing XML:', err);
        return;
      }
      const types = result.Package.types;
      types.forEach((type) => {
        const typeName = type.name[0];
        const members = type.members.map((member) => member.trim());
        addMembers(typeName, members);
      });
    });
  };

  parseXml(xml1);
  parseXml(xml2);

  const sortedTypes = Object.keys(mergedTypes).sort();
  const builder = new Builder({ headless: true });
  const mergedXmlObj = {
    Package: {
      $: {
        xmlns: 'http://soap.sforce.com/2006/04/metadata',
      },
      types: sortedTypes.map((type) => ({
        name: type,
        members: Array.from(mergedTypes[type]).sort(),
      })),
      version: '61.0',
    },
  };

  const mergedXml = builder.buildObject(mergedXmlObj);
  const formattedXml = new XMLSerializer().serializeToString(new DOMParser().parseFromString(mergedXml, 'application/xml'));

  return `<?xml version="1.0" encoding="UTF-8"?>\n${formattedXml}`;
};
